*{
    margin: 0;
    padding: 0;
}
body{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(0, 0, 54);
}
nav{
    position: absolute;
    top: 0;
    width: 100vw;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}
button{
    height: 60px;
    width: min(360px, 90vw);
    color: aliceblue;
    font-size: 24px;
    border: none;
    background-color: transparent;
}
.root{
    margin-top: 60px;
    height: calc(100vh - 60px);
    overflow: scroll;
    width: 100vw;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background-color: rgb(48, 0, 93);
}
.container{
    border: 5px solid rgb(162, 0, 255);
    border-radius: 15px;
    position: relative;
    width: min(360px, 90vw);
    height: min(480px, 120vw);
    margin: min(30px, 9vw) min(30px, 4vw);
    overflow: hidden;
    background-color: rgb(0, 96, 199);
}
.card{
    background-image: linear-gradient(white, rgb(85, 0, 182),rgb(0, 27, 201));
    position: relative;
    height: min(960px, 240vw);
    width: 100%;
    top: 0;
    transition-duration: 400ms;
    transition-timing-function: ease-in-out;
}
.up{
    top: -100%;
}
.place{
    width: 100%;
    text-align: center;
}
h3, h2, h1{
    font-family: "karla", system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: aliceblue;
    margin: 10px;
}
.b{
    border-bottom: 5px solid greenyellow;
}
.up{
    top: -100%;
}
.imgc{
    width: min(360px, 90vw);
    height: min(480px, 120vw);
    object-fit: cover;
}
img{
    height: 100%;
    width: 100%;
}
.name{
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: end;
    text-align: end;
    font-size: 24px;
    height:  min(120px, 30vw);
    top:  min(360px, 90vw);
    background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.7));
}
.social{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 70px;
    width: 80%;
    margin: auto;
}
a{
    margin-top: 10px;
    height: 55px;
    width: 55px;
}
span{
    color: black;
}